import { useContext, useRef, ReactNode } from 'react';
import { lighten } from 'polished';

import BareButton from '../Button/BareButton';
import Drawer from '../Drawer';
import Flex from '../Flex';
import Icon, { registerIcon } from '../Icon';
import * as arrowLeftIcon from '../Icon/icons/arrow-left.icon';
import Text from '../Text';
import { useDirection, useEscape, useFocusTrap, useI18n, useTheme, useUID } from '../../hooks';
import Configuration from '../Configuration';
import { tryCatch } from '../../utils';

import AppShellContext from './AppShellContext';
import {
  StyledDrawerWrapper,
  StyledDrawerBackButton,
  StyledDrawerHeading,
  StyledDrawerListContainer
} from './Drawer.styles';

registerIcon(arrowLeftIcon);

interface DrawerViewProps {
  content: ReactNode;
  header: string;
  onDrawerOpen?: () => void;
  onDrawerClose?: () => void;
  drawerOpen: boolean;
  setDrawerOpen: (open: boolean) => void;
}

export default ({
  content,
  header,
  onDrawerOpen,
  onDrawerClose,
  drawerOpen,
  setDrawerOpen
}: DrawerViewProps) => {
  const { focusedImperatively } = useContext(AppShellContext);
  const returnFocusRef = useRef<HTMLElement>();
  const drawerRef = useRef<HTMLDivElement>(null);
  const theme = useTheme();
  const headerId = useUID();

  const backButtonRef = useRef<HTMLButtonElement>(null);
  const appShellBg = tryCatch(() =>
    lighten(0.1, theme.components['app-shell'].nav['background-color'])
  );
  const appShellTheme = {
    base: {
      palette: {
        'primary-background': appShellBg,
        'foreground-color': 'rgba(255, 255, 255, 0.7)',
        interactive: theme.base.colors.blue.light
      }
    }
  };

  const { ltr, rtl } = useDirection();
  const t = useI18n();

  useEscape(
    () => {
      setDrawerOpen(false);
    },
    drawerRef,
    [drawerRef.current]
  );

  useFocusTrap(drawerRef);

  return (
    <Configuration theme={appShellTheme}>
      <Drawer
        role='dialog'
        aria-labelledby={headerId}
        as={StyledDrawerWrapper}
        position='fixed'
        placement='left'
        open={drawerOpen}
        nullWhenClosed
        onBeforeOpen={() => {
          returnFocusRef.current = (document.activeElement as HTMLElement) ?? undefined;
        }}
        onAfterOpen={() => {
          if (backButtonRef.current && document.activeElement !== backButtonRef.current) {
            focusedImperatively.current = true;
            backButtonRef.current.focus();
          }
          onDrawerOpen?.();
        }}
        onAfterClose={() => {
          onDrawerClose?.();
          returnFocusRef.current?.focus();
        }}
        onOuterClick={() => setDrawerOpen(false)}
        ref={drawerRef}
      >
        <Flex as={StyledDrawerHeading} container={{ alignItems: 'center' }}>
          {rtl && (
            <Text variant='h2' id={headerId}>
              {header}
            </Text>
          )}
          <BareButton
            as={StyledDrawerBackButton}
            ref={backButtonRef}
            onClick={() => setDrawerOpen(false)}
            aria-label={t('close_noun', [header])}
          >
            <Icon name='arrow-left' />
          </BareButton>
          {ltr && (
            <Text variant='h2' id={headerId}>
              {header}
            </Text>
          )}
        </Flex>
        <Flex
          as={StyledDrawerListContainer}
          container={{ direction: 'column' }}
          aria-labelledby={headerId}
        >
          {content}
        </Flex>
      </Drawer>
    </Configuration>
  );
};
